const createGalleryCard = imageInfo => {
  return `<li class="gallery-item"> <a class="gallery-link" href="${imageInfo.original}">
    <img class="gallery-image" src="${imageInfo.preview}" alt="${imageInfo.description}" width="360" height="200" />  </a>
  </li>`;
};
const galleryCardTemplate = images
  .map(imageInfo => createGalleryCard(imageInfo))
  .join('');
const galleryListEl = document.querySelector('.gallery');
galleryListEl.innerHTML = galleryCardTemplate;
// initialization SimpleLightbox
new SimpleLightbox('.gallery a', {
  nav: true,
  captions: true,
  captionSelector: 'img',
  captionType: 'attr',
  captionsData: 'alt',
  captionPosition: 'bottom',
  captionDelay: 250,
  captionHTML: true,
  close: true,
  closeText: 'x',
  docClose: true,
  showCounter: true,
  fileExt: 'png|jpg|jpeg|gif',
  enableKeyboard: true,
  //   className: 'simple-lightbox',
  widthRatio: 0.8,
  heightRatio: 0.9,
  alertError: true,
  alertErrorMessage: 'Image not found, next image will be loaded',
  download: false,
});
